import React from "react"
import Banner from "./Banner"
import Contact from "./Contact"
import Footer from "./Footer"
import Main from "./Main"
import { Link } from "gatsby"
import { StaticQuery, graphql } from "gatsby"

const Index = props => (
  <StaticQuery
    query={graphql`
      query {
        allFile(
          filter: {
            sourceInstanceName: { eq: "content" }
            name: { eq: "homepage" }
          }
        ) {
          edges {
            node {
              childMarkdownRemark {
                frontmatter {
                  title
                  content {
                    bannerheader
                    subtitle
                    contactheader
                    contactbody
                  }
                  article {
                    headline
                    content
                    photo
                  }
                }
              }
            }
          }
        }
      }
    `}
    render={data => (
      <main className="is-preload">
        <div id="wrapper">
          {/* Menu */}
          <header id="header" className="alt">
            <Link to="/" className="logo">
              {/* <h1>{data.title}</h1> */}
              <strong>Ashley's</strong> <span>Animal Adoption</span>
            </Link>
            <nav>
              <a href="/Menu">Menu</a>
            </nav>
          </header>

          {/* Banner */}
          <Banner
            test={data.title}
            header={data.content[0].bannerheader}
            subtitle={data.content[0].subtitle}
          />

          {/* Main */}
          <Main
            contactheader={data.content[0].contactheader}
            contactbody={data.content[0].contactbody}
            article={data.article}
          />
          {/* Contact */}
          <Contact />

          {/* Footer */}
          <Footer />

          {/* Scripts */}
          <script src="assets/js/jquery.min.js"></script>
          <script src="assets/js/jquery.scrolly.min.js"></script>
          <script src="assets/js/jquery.scrollex.min.js"></script>
          <script src="assets/js/browser.min.js"></script>
          <script src="assets/js/breakpoints.min.js"></script>
          <script src="assets/js/util.js"></script>
          <script src="assets/js/main.js"></script>
        </div>
      </main>
    )}
  />
)
export default Index
